@import '../../const/index.scss';
button{
    background-color: transparent;
    border: transparent;
}
.containerMenu{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: $primary;
}
.containerRowLogo{
    width: 100%;
    max-height: 100px;    
    display: flex;
    flex-direction: column;
    .containerLogo{
        width: 15%;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width: 100px;
        }
    }
}
.containerRowHeader{
    width: 100%;
    min-height: 70px;    
    display: flex;
    flex-direction: column;    
   
    .containerIcon{
        width: 15%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        transition-duration: 2s;
        button{
            border: none;
            background-color: transparent;            
        }
        img{
            width: 50%;
            max-height: 70%;
        }
        p{
            max-height: 30%;
            font-size: 0.6rem;
        }
    }
    .containerIcon:hover{
        opacity: 0.8;
        p{
            color: $white;
        }
        img{
            filter: invert(100%);
        }
    }
}


.containerHeaders{
    width: 100%;
    margin: 0;
    padding-left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;    

    .RowHeader{
        width: 100%;
        height: 50px;
        background-color: $primary;
        .containerHeaderTitle{
            margin-left: 20px;
            width: 70%;            
            height: 100%;
            display: flex;
            align-items: center;
            button{
                
                width: 100%;
                height: 100%;
                text-decoration: none;
                font-size: 1.5rem;
                font-weight: 700;
                color: $white;                    
                transition-duration: 1s;
            }            
        }
        .containerHeaderTitle:hover{            
            background-color: $accent;
            button{
                font-size: 1.8rem;
            }               
        }
    }
}

.containerHeaderHiden{
    display: none;
}


