@import '../../const/index.scss';

.contactContainer{
    display: flex;
    width: 100vw;
    height: 100vh;
    background-color: $primary;
}
.contactLeftContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30vw;
    height: 100vh;
}
.contactRightContainer{
    display: flex;   
    justify-content: center;
    align-items: center;
    width: 70vw;
    height: 100%;    
    form{
        width: 50%;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: space-around;
        align-items: center;
        label{            
            color: $variantAccent;
            font-size: 1.5em;
            font-weight: 700;
            margin-top: 12px;
            margin-bottom: 5px;
        }
        input{
            text-align: center;
            width: 80%;
            height: 2em;
            font-size: 1em;
            font-weight: 500;
            border-radius: 10px;
        }
        input:hover{
            border: solid $accent;
        }
        .message{
            height: 6em;
        }
        button{
            transition-duration: 0.5s;
            margin-top: 30px;
            border-radius: 20px;
            width: 50%;
            height: 2.5em;
            font-size: 1em;
            font-weight: 600;
            background-color: $accent;
            color: white;
        }
        button:hover{
            font-size: 1.2em;
            font-weight: 700;
        }
    }
}