@import '../../../const/index.scss';
.CarruselContainer{
  background-color: transparent;
  justify-content: center;
  align-items: center;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  .slideContainer{
    min-width: 100%;
    min-height: 80vh;
    margin-left: 0;
    margin-bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;

    .containerWindowWork{
      min-width: 100%;
      min-height: 80vh;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-wrap: nowrap;
      .containerTitleWork{        
        color: $white;
        position: absolute;
        min-width: 80vw;
        min-height: 10vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        font-weight: 800;
        .sitio{
          color: $white;
          font-size: 1rem;
        }

      }
      .containerWorkMovile{
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        margin-top: 10vh;
        margin-left: -40vw;
        min-width: 40vw;
        min-height: 70vh;
        img{
          position: absolute;
          z-index: 1;
          width: 50%;
        }
        .containerDescription{
          position: absolute;
          z-index: 2;        
          background-color: $white;
          opacity: 0.5;
          display: flex;
          justify-content: center;
          align-items: center;
          .contentDescription{
            width: 95%;
            height: 95%;
            background-color: $accent;
          }
        }
      }
      .containerWorkMovile:hover{
        .containerDescription{
          width: 70%;
          height: 70%;
          animation: viewDescription 1s;
        }
      }
      .containerWorkComputer{
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        margin-top: 10vh;
        margin-left: 40vw;
        min-width: 40vw;
        min-height: 70vh;
        img{
          position: absolute;
          z-index: 1;
          width: 50%;
        }
        .containerDescription{
          position: absolute;
          z-index: 2;        
          background-color: $white;
          opacity: 0.5;
          display: flex;
          justify-content: center;
          align-items: center;
          .contentDescription{
            width: 95%;
            height: 95%;
            background-color: $accent;
          }
        }                    
      }
      .containerWorkComputer:hover{
        .containerDescription{
          width: 70%;
          height: 70%;
          animation: viewDescription 1s;
        }
      }

    }
  }
  
}

@keyframes viewDescription {
  0%{
    width: 0%;
    height: 0%;
  }
  100%{
    width: 70%;
    height: 70%;
  }
}
