@import '../../../const/index.scss';

.containerButtonBack{
    width: 8vh;
    height: 8vh;
    position: absolute;
    margin-top: 40vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    transition-duration: 1s;
    font-size: 5vh;
    .containerTextBack{
        width: 4vh;
        height: 8vh;
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        color: $accent;
    }
}
.containerButtonBack:hover {
    font-size: 8vh;
    .containerTextBack{
        color: $variantPrimary;
    }
}

.containerButtonNext{
    position: absolute;
    width: 8vh;
    height: 8vh;
    margin-top: 40vh;
    margin-left: 95vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    transition-duration: 1s;
    font-size: 5vh;
    color: $accent;
    .containerTextNext{
        width: 4vh;
        height: 8vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;     
        color:$accent;
    }
}
.containerButtonNext:hover {
    font-size: 8vh;    
    .containerTextNext{        
        color: $variantPrimary;
    }
}


/*BOTONES MENU*/

.containerHeadersButtons{
    height: 5vh;
    width: 30vw;
    position: absolute;
    margin-top: 0;
    display: flex;
    div{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        font-weight: 800;
        img{
            height: 5vh;
        }
    }
    .homeButtonContainer{
        height: 5vh;
        width: 6vw;
        filter: invert(70%);
        .homeTextContainer{
            width: 3vw;
            position: absolute;
            margin-left: 0;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;
            color: transparent;
        }
    }
    .homeButtonContainer:hover{
        filter: invert(100%);        
        .homeTextContainer{
            position: absolute;
            animation: SalirMenu 10s;
        }
    }
    
    .aboutButtonContainer{
        height: 5vh;
        width: 6vw;
        filter: invert(70%);
        .aboutTextContainer{
            width: 3vw;
            position: absolute;
            margin-left: 0;
