@import '../../../const/index.scss';
.center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.main {
    
    height: 100vh;
}

@keyframes AparecerTexto {
    0%{
        width: 50%;        
    }
    100%{
        width: 100%;
        
    }
}

.box{
    flex-direction: column;
    a{
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        border-radius: 50%;
        color: rgb(51,51,51);
        margin: 0.3em;
        background:  rgb(236, 236, 236);
        position: relative;
        transition: all 0.4s;

        img{
            width: 60%;
            z-index: 20;
            transition: all 0.4s;
        }
        label{
            display: none;
        }
    }
    a:hover{
        transform: scale(0.9);
        color: white;
        img{
            position: absolute;            
            z-index: 5;
            filter: invert(100%);
        }
        div{
            min-width: 180px;
            height: 60px;
            margin-left: 240px;

            label{
                z-index: -1;
                color: #fff;
                background: #e94057e1;
                font-size: 1.2rem;
                font-weight: 500;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 50px;
                height: 60px;
                animation: AparecerTexto 2s;
            }
        }
        
    }

    a::before{
        content: attr(data);
        position: absolute;
        top: 0;
        left: 70px;
        font-family: cursive;
        border-radius: 50px;
        width: 150px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        color: rgb(51, 51, 51);
        background: rgb(236, 236,236);
        display: none;
    }
    a::hover::before{
        display: flex;
    }

    a::after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 50px;
        width: 60px;
        height: 60px;
        background: #e94057e1;
        transform: scale(0);
        transition: all 0.4s;
    }
    a:hover::after{
        transform: scale(0.8);
    }
}