@import '../../../const/index.scss';

@media (min-width: 1200px) {

.world {
    perspective: 1000px;
}

.cube {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    width: 60vw;
    height: 60vh;
    transform-style: preserve-3d;
    animation-name: animateWorld;
    animation-duration: 25s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.circunferenciaCero, .circunferenciaUno,.circunferenciaDos, .circunferenciaTres, .circunferenciaCuatro, .circunferenciaCinco, .circunferenciaSeis {
    display: flex;
    justify-content: center;
    align-items: center;
    transform-style: preserve-3d;    
    position: absolute;
    width: 100%;    
    height: 100%;
    border: 2px dashed #E0FBFC;
    border-radius: 50%;
    opacity: 0.8;
    background: rgba(255, 255, 255, 0);
    
    img{
        width: 10vw;
        height: 10vh;
        margin: 10vw;
    }
}

.circunferenciaCero {    
    width: 100%;
    height: 100%;
    transform: rotateX(90deg);
}

.circunferenciaUno {
    transform: rotateY(45deg);
}

.circunferenciaTres {
    transform: rotateY(90deg);
}

.circunferenciaCuatro {
    transform: rotateY(135deg);
}

.circunferenciaCinco {
    width: 60vw;
    height: 35vw;
    border: transparent;
    margin: 5vh;
    transform: rotateX(90deg) translateZ(15vw);
}

.circunferenciaSeis{
    border: transparent;
    width: 60vw;
    height: 35vw;
    margin: 5vw;
    transform: rotateX(90deg) translateZ(-10vw);

}

@keyframes animateWorld {
    0% { -webkit-transform: rotateY(0deg) rotateX(0deg) rotateZ(0deg); }
    50% { -webkit-transform: rotateY(360deg) rotateX(180deg) rotateZ(180deg); }
    100% { -webkit-transform: rotateY(720deg) rotateX(360deg) rotateZ(360deg); }
}
}



@media (max-width: 1200px) {

    .world {
        perspective: 1000px;
    }
    
    .cube {
        margin-left: auto;
        margin-right: auto;
        position: relative;
        width: 80vw;
        height: 80vh;
        transform-style: preserve-3d;
        animation-name: animateWorld;
        animation-duration: 25s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }
    
    .circunferenciaCero, .circunferenciaUno,.circunferenciaDos, .circunferenciaTres, .circunferenciaCuatro, .circunferenciaCinco, .circunferenciaSeis {
        display: flex;
        justify-content: center;
        align-items: center;
        transform-style: preserve-3d;    
        position: absolute;
        width: 150%;    
        height: 100%;
        border: 2px dashed $accent;
        border-radius: 50%;
        opacity: 0.8;
        background: rgba(255, 255, 255, 0);
        
        img{
            width: 10vw;
            height: 10vh;
            margin: 10vw;
        }
    }
    
    .circunferenciaCero {    
        width: 100%;
        height: 90%;
        transform: rotateX(90deg);
    }
    
    .circunferenciaUno {
        transform: rotateY(45deg);
    }
    
    .circunferenciaTres {
        transform: rotateY(90deg);
    }
    
    .circunferenciaCuatro {
        transform: rotateY(135deg);
    }
    
    .circunferenciaCinco {
        width: 60vw;
        height: 35vw;
        border: transparent;
        margin: 5vh;
        transform: rotateX(90deg) translateZ(15vw);
    }
    
    .circunferenciaSeis{
        border: transparent;
        width: 60vw;
        height: 35vw;
        margin: 5vw;
        transform: rotateX(90deg) translateZ(-10vw);
    
    }
    
    @keyframes animateWorld {
        0% { -webkit-transform: rotateY(0deg) rotateX(0deg) rotateZ(0deg); }
        50% { -webkit-transform: rotateY(360deg) rotateX(180deg) rotateZ(180deg); }
        100% { -webkit-transform: rotateY(720deg) rotateX(360deg) rotateZ(360deg); }
    }
    }