@import '../../const/index.scss';


@media (min-width: 1200px) {
.aboutmeContainer{
    width: 100vw;
    height: 100vh;
    background-color: $primary;
}
.fondo1{
    position: absolute;
    z-index: 1;
    width: 90vw;
    margin-right: 10vw;
    margin-top: 20vh;
    height: 50vh;
    background-color: $accent;
}
.fondo2{
    position: absolute;
    z-index: 1;
    width: 90vw;
    margin-left: 10vw;
    margin-top: 30vh;
    height: 50vh;
    background-color: $accent;
}
.containerContent{
    position: absolute;
    z-index: 5;
    width: 100vw;    
    margin-top: 25vh;
    height: 50vh;
    background-color: $variantAccent;
    display: flex;
}
.ImgAboutMe{
    width: 40vw;
    display: flex;
    justify-content: center;
    align-items: center;
    
    img{
        width: 40%;
        height: 100%;
        border: 10px solid $accent;
        border-radius: 50%;
        
    }
}
.textContainerAboutMe{
    height: 100%;
    width: 60vw;
    p{
        width: 80%;
        margin-top: 20%;
        text-align: justify;
        font-size: 1.2rem;
    }
}
}




@media (max-width: 1200px) {
    .aboutmeContainer{
        width: 100vw;
        height: 100vh;
        background-color: $primary;
    }
    .fondo1{
        position: absolute;
        z-index: 1;
        width: 90vw;
        margin-right: 10vw;
        margin-top: 5vh;
        height: 80vh;
        background-color: $accent;
    }
    .fondo2{
        position: absolute;
        z-index: 1;
        width: 90vw;
        margin-left: 10vw;
        margin-top: 15vh;
        height: 80vh;
        background-color: $accent;
    }
    .containerContent{
        position: absolute;
        z-index: 5;
        width: 100vw;    
        margin-top: 10vh;
        height: 80vh;
        background-color: $variantAccent;
        display: flex;
    }
    .ImgAboutMe{
        width: 40vw;
        display: flex;
        justify-content: center;
        align-items: center;
        
        img{
            width: 80%;
            height: 60%;
            border: 5px solid $accent;
            border-radius: 50%;
            
        }
    }
    .textContainerAboutMe{
        height: 100%;
        width: 60vw;
        display: flex;
        justify-content: center;
        align-items: center;
        p{
            width: 80%;
            margin-top: 20%;
            text-align: justify;
            font-size: 1rem;
        }
    }
    }