@import '../../const/index.scss';
$fondoLogo : #073c81;



@media (min-width: 1200px) {
    .principalContainerw{
        width: 100vw;
        height: 100vh;
        display: flex;
    }
    .containerPresentation{
        position: absolute;
        z-index: 1;
        animation: QuitarHello 8s;
        width: 100vw;
        height: 100vh;
        background-color: $primary;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        opacity: 0;
    }
    .containerHello{
        width: 40vw;
        height: 20vh;
        display: flex;
        justify-content: center;
        align-items: center;
        display: flex;
        div{
            color: $accent;
            width: 20%;
            display: flex;
            justify-content: center;
            align-items: center;        
            font-size: 10vw;
            font-weight: 800;
            font-family:Verdana, Geneva, Tahoma, sans-serif;
        }
    }
    #H{
        animation: AgrandarH 3s;
    }
    .E{
        animation: AgrandarE 3s;
    }
    .L1{
        animation: AgrandarL1 3s;
    }
    .L2{
        animation: AgrandarL2 3s;
    }
    .O{
        animation: AgrandarO 3s;
    }
    @keyframes AgrandarH {
        0%{
            color: $accent;
            transform: translateX(10vw);        
            opacity: 0;
        }
        50%{
            color: $accent;
            transform: none;
            opacity: 1;
        }
        95%{
            transform: translateX(100px) scale(0.5);
            opacity: 0;
        }
        100%{            
            opacity: 0;
        }    
    }
    @keyframes AgrandarE {
        0%{      
            color: $accent;  
            transform: translateX(5vw);        
            opacity: 0;
        }
        50%{      
            color: $accent;    
            transform: none;
            opacity: 1;
        }   
        100%{
            transform: translateX(100px) scale(0.5);
            opacity: 0;
        }    
    }
    @keyframes AgrandarL1 {
        0%{
            color: $accent;
            opacity: 0;
        }
        50%{
            color: $accent;
            transform: translateX(100px) scale(1);
            transform: none;
            opacity: 1;
        }
        100%{
            transform: translateX(100px) scale(0.5);
            opacity: 0;
        }    
    }
    @keyframes AgrandarL2 {
        0%{
            color: $accent;
            transform: translateX(-5vw); 
            opacity: 0;
        }
        50%{
            color: $accent;
            transform: none;
            opacity: 1;
        }
        100%{
            transform: translateX(100px) scale(0.5);
            opacity: 0;
        }    
    }
    @keyframes AgrandarO {
        0%{
            color: $accent;
            transform: translateX(-10vw); 
            opacity: 0;
        }
        50%{
            color: $accent;
            transform: none;
            opacity: 1;
        }
        100%{
            transform: translateX(100px) scale(0.5);
            opacity: 0;
        }        
    }
    @keyframes AgrandarO {
        0%{
            color: $accent;
            transform: translateX(-10vw); 
            opacity: 0;
        }
        50%{
            color: $accent;
            transform: none;
            opacity: 1;
        }
        100%{
            transform: translateX(100px) scale(0.5);
            opacity: 0;
        }        
    }   
    @keyframes QuitarHello {
        0%{
            opacity: 1;
        }
        50%{
            display: flex;
            opacity: 1;
        }
        100%{
            transform: translateX(-80vw);
            opacity: 0;    
            visibility: hidden;
        }    
    }
    .containerHome{
        position: absolute;
        z-index: -1;
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: row;
    }
    .containerLeft{
        width: 30%;
        height: 100%;
        background-color: $accent;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        div{
            font-size: 2vw;
            font-weight: 600;
            width: 2vw;
            text-align: center;
        }
    }
    .containerRight{
        width: 70%;
        height: 100%;
        background-color: $primary;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;  
        flex-wrap: wrap;     
        img{
            width: 50%;
            margin-left: 25%;
        }
        .sub{    
            color: $variantAccent;
            display: flex;
            font-size: 4em;
            justify-content: center;
            width: 90%;
            text-align: right;
            font-weight: 600;
            $time1 : 6s;
            $time2 : 7s;
            $time3 : 8s;
            $time4 : 9s;
            $time5 : 10s;
            .letra-F{
                animation: Aparecer $time1;
            }
            .letra-U{
                animation: Aparecer $time2;
            }
            .letra-L{
                animation: Aparecer $time3;
            }
            .letra-S{
                animation: Aparecer $time4;
            }
            .letra-T{
                animation: Aparecer $time5;            
            }
            .letra-A{
                animation: Aparecer $time1;
            }
            .letra-C{
                animation: Aparecer $time2;
            }
            .letra-K{
                animation: Aparecer $time3;      
            }
            .letra-D{
                animation: Aparecer $time4;
            }
            .letra-E{
                animation: Aparecer $time5;
            }
            .letra-V{
                animation: Aparecer $time1;
            }
            .letra-O{
                animation: Aparecer $time2;
            }
            .letra-P{
                animation: Aparecer $time3;      
            }
            .letra-M{
                animation: Aparecer $time4;      
            }
            .letra-N{
                animation: Aparecer $time5;      
            }
        }
        div{
            display: flex;
            flex-direction: row;
            text-align: center;    
            div{
                display: flex;
                flex-direction: row;
                width: 100%;
                font-size: 2vw;
                font-weight: 600;
                width: 2vw;
                justify-content: center;
            }
        }
    }

    @keyframes Aparecer {
        0%{
            opacity: 0;
            visibility: hidden;
        }
        40%{
            opacity: 0;
            visibility: hidden;
        }
        100%{
            opacity: 1;
            visibility: visible;
        }
    }
}



