@import '../../const/index.scss';

@media (min-width: 1200px) {
.habilitiesContainer{
    width: 100vw;
    height: 90vh;
    padding-top: 10vh;
    background-color: $primary;
    display: flex;
    flex-direction: row;
    color: $white;
}
.lefthContainer{
    width: 60vw;
    height: 100%;
}
.headerContainer{
    width: 100%;
    padding-left: 10vw;
    padding-right: 10vw;
    h3{
        width: 70%;
        text-align: center;
        font-size: 1.8rem;
    }
}
.frontContainer, .endContainer, .otherContainer{
    width: 100%;
    padding-left: 10vw;
    display: flex;
    flex-direction: column;
    p{
        width: 70%;
        text-align: center;
        font-size: 1.2rem;
        font-weight: 500;
    }
    div{
        width: 100%;
        display: flex;
        flex-wrap: nowrap;
        ul{
            width: 50%;
            li{
              transition-duration: 1s;
            }
            li:hover{
                font-size: 1.2rem;
                color: $accent;
               
                text-decoration: underline $white;
            }
        }        
    }
}

.rightContainer{
    position: absolute;
    width: 40vw;
    margin-left: 60vw;
    height: 90vh;
}
}


@media (max-width: 1200px) {
    .habilitiesContainer{
        width: 100vw;
        height: 100vh;
        background-color: $primary;
        display: flex;
        flex-direction: row;
        color: $white;
    }
    .lefthContainer{
        width: 100vw;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .headerContainer{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h3{            
            width: 100%;
            height: 50%;
            text-align: center;
            font-size: 1.2rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        p{padding-left: 10px;}

    }
    .frontContainer, .endContainer, .otherContainer{
        width: 100%;
        padding-left: 10vw;
        display: flex;
        flex-direction: column;
        p{
            width: 100%;
            text-align: center;
            font-size: 1rem;
            font-weight: 500;
        }
        div{
            width: 100%;
            display: flex;
            flex-wrap: nowrap;
            ul{
                width: 50%;
                li{
                    font-size: 0.8rem;
                  transition-duration: 1s;
                }
                li:hover{
                    font-size: 1.2rem;
                    color: $accent;
                   
                    text-decoration: underline $white;
                }
            }        
        }
    }
    
    .rightContainer{
        position: absolute;
        width: 40vw;
        margin-left: 60vw;
        height: 90vh;
    }
    }